    justify-content: space-between;
    align-items: stretch;
    iframe {
      height: 100%;
    }
  }

  .find-contact__name {
    grid-area: link-name;
  }
  .find-contact__link {
    grid-area: link;

    &:nth-child(2) {
      grid-area: link2;
    }
  }

  .find-contact__item {
    display: grid;
    grid-gap: 11px 5px;
    align-items: flex-start;
    justify-content: flex-start;
    grid-template-columns: auto auto;
    grid-template-areas:
      "link-name link2"
      "link-name link";
  }

  // ------------- services

  .container-mode--translate {
    transform: none;
  }

  .first-sec__box {
    padding: 0 40px;
  }

  .first-sec__title {
    font-size: 35px;
  }


  .services-third__box {
    display: grid;
    grid-template-columns: 400px 1fr !important;
    grid-gap: 20px;
  }

  .services-third__article {
    width: 100%;
  }

  .services-third__slider {
    max-width: 700px;
    width: 100%;
  }

  .services-third {
    padding-bottom: 50px;
  }

  .service-list__inner {
    width: 100%;

    img, .service-list__image  {
      height: 100%;
      width: 100%;
    }
  }

  .services-single-first__box {
    padding: 0 0 0 90px;
    grid-gap: 50px;
  }

  .services-single-first__image {
    margin-right: -50px;
  }

  .services-single-second__box {
    padding: 0 30px;
  }

  .point-line::before {
    height: initial;
    bottom: -30px;
    top: 40px;
    background-size: contain;
    background-repeat: no-repeat;
  }

     // ----------- about -------------

     .about-second {
      padding: 40px 0 60px;
      background-size: cover;
      background-position: center;
    }

    .pas-section--about {
      padding-top: 60px;
      padding-bottom: 60px;
    }

    .about-tabs-sec {
      padding-top: 40px;
      padding-bottom: 60px;
    }

    .about-tabs__list {
      grid-gap: 25px;
      justify-content: center;
    }

    .sub-tabs {
      grid-gap: 20px;
    }

    .about-second,
    .pas-section--about,
    .about-tabs-sec {
      padding-left: 30px;
      padding-right: 30px;
    }



    // ---------- blog-single --------

    .blog-single-second {
      .container {
        padding: 0 30px;
      }

      .blog-single-second__item {
        width: 100%;
      }

      .blog-list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 20px;
      }
    }

     //  contacts


  .contact-sec {
    padding: 45px 0 50px;

    &__box {
      grid-template-columns: 250px 1fr;
      padding: 0 20px;
      grid-gap: 20px;
      max-width: 992px;
      margin: 0 auto;
    }

    &__list {
      grid-gap: 20px;
    }

    &__title {
      padding-left: 43px;
      font-size: Min(4.375vw, 24px);
